import React, { useState } from 'react';
import { Button, Form, Input, Card } from 'antd';
import './contact.css';

const validateMessages = {
  required: (label) => `${label} l bt buc!`,
  types: {
    email: (label) => `${label} khng hp l!`,
    number: (label) => `${label} phi l s!`,
  },
  number: {
    range: (label, min, max) => `${label} phi nm trong khong ${min} v ${max}`,
  },
};


const ContactForm = () => {
  const [isRegister, setIsRegister] = useState(false);

  const onFinish = (values) => {
    console.log('D liu:', values);
  };

  return (
    <div className="auth-container">
      <Card className="auth-card">
        <h2>{isRegister ? 'ng k' : 'ng nhp'}</h2>
        <Form 
          name="auth-form"
          onFinish={onFinish}
          validateMessages={validateMessages}
          layout="vertical"
        >
          {isRegister && (
            <Form.Item name="name" label="H v tn" rules={[{ required: true }]}>
              <Input placeholder="Nhp h v tn" />
            </Form.Item>
          )}

          <Form.Item name="email" label="Email" rules={[{ required: true, type: 'email' }]}>
            <Input placeholder="Nhp email" />
          </Form.Item>

          <Form.Item name="password" label="Mt khu" rules={[{ required: true }]}>
            <Input.Password placeholder="Nhp mt khu" />
          </Form.Item>

          {isRegister && (
            <Form.Item 
              name="confirmPassword" 
              label="Xc nhn mt khu"
              dependencies={['password']}
              rules={[
                { required: true, message: 'Vui lng xc nhn mt khu!' },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error('Mt khu khng khp!'));
                  },
                }),
              ]}
            >
              <Input.Password placeholder="Nhp li mt khu" />
            </Form.Item>
          )}

          <Form.Item>
            <Button type="primary" htmlType="submit">
              {isRegister ? 'ng k' : 'ng nhp'}
            </Button>
          </Form.Item>

          <Form.Item>
            <Button type="link" onClick={() => setIsRegister(!isRegister)}>
              {isRegister ? ' c ti khon? ng nhp' : 'Cha c ti khon? ng k'}
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};

export default ContactForm;
