import React from "react";
import "./VideoSection.css";

const VideoSection = ({ onShowCompanyInfo }) => {
  return (
    <section className="video-section">
      <video className="video-bg" autoPlay loop muted controls>
        <source
          src="https://interactive-examples.mdn.mozilla.net/media/cc0-videos/flower.mp4"
          type="video/mp4"
        />
        Your browser does not support the video tag.
      </video>
      <div className="video-overlay">
        <div className="video-container text-center">
          <h1 className="display-4">Gii php tin tin cho mi trng</h1>
          <p className="lead">
            Cng ngh, ti nguyn v mi trng  ng hnh cng s pht trin bn
            vng
          </p>
          <button className="btn btn-primary" onClick={onShowCompanyInfo}>
            Tm hiu thm
          </button>
        </div>
      </div>
    </section>
  );
};

export default VideoSection;
